import React, { useEffect, useState } from 'react';
import defaultData from './defaultData';

interface SectionProps extends React.HTMLProps<HTMLDivElement> {
  id: string;
  data?: any;
}

const Section: React.FC<SectionProps> = props => {
  const { id } = props;

  return (
    <section
      id={id}
      className={id === 'contact' ? '' : 'bg-white'}
      style={{
        padding: '6.86rem 0'
      }}
    >
      <div
        className={`container${
          id === 'contact' ? ' bg-white px-sm-5 py-5' : ''
        }`}
      >
        <div className='mb-5 text-center'>
          <h2
            className='mb-1'
            style={{
              letterSpacing: '0.1rem'
            }}
          >
            {defaultData.sections[id].title}
          </h2>
          <p>{defaultData.sections[id].title_ja}</p>
        </div>
        {props.children}
      </div>
    </section>
  );
};

export default Section;
